import React from 'react';
import { Switch, Route } from 'react-router-dom';

import LandingPage from './landingpage';
import AboutMe from './aboutme';
import Contact from './contact';
import Projects from './projects';
import Resume from './resume';

const Main = () => (
    <Switch>
        <Route exact path="/react-mdl-portfolio" component={LandingPage} />
        <Route path="/react-mdl-portfolio/aboutme" component={AboutMe} />
        <Route path="/react-mdl-portfolio/contact" component={Contact} />
        <Route path="/react-mdl-portfolio/projects" component={Projects} />
        <Route path="/react-mdl-portfolio/resume" component={Resume} />
    </Switch>
)

export default Main;